import React, { useEffect, useState } from "react";
import { Breadcrumb, Table } from "antd";
import MainLayout from "../layouts/home";
import axios from "axios";
import { useLoaderData } from "@remix-run/react";
import { SearchBar } from "../user/search"
import { Col, Row } from 'antd';
export async function getExpenseList(id) {
  try {
    const res = await axios.get(`http://localhost:4000/api/expense/user/${id}`);
    const mydata = await res?.data;
    return { mydata };
  } catch (error) {
    console.log(error.message, "error");
    return { error };
  }
}
export async function loader({ params }) {
  // const userId = await getUserId(params.id);
  const userId = "639c1738bebc82f02476e66a";
  const expenseList = await getExpenseList(userId);
  return expenseList;
}

const columns = [
  {
    title: "ItemName",
    dataIndex: "title",
    key: "title",
    render: (text) => <a>{text}</a>,
  },
  {
    title: "Amount",
    dataIndex: "amount",
    key: "amount",
  },
  {
    title: "Category",
    dataIndex: "category",
    key: "category",
  },
  {
    title: "ExpenseDate",
    dataIndex: "createdAt",
    key: "createdAt",
  },
];
function Report() {
  const loaderData = useLoaderData();
  const [expenses, setExpenses] = useState([]);
  console.log("loaderdata", expenses);
  useEffect(() => {
    const data = loaderData?.mydata?.user?.expenses;
    setExpenses(data);
  }, [loaderData]);
  return (
    <MainLayout>
      <div>
        <Breadcrumb
          style={{
            margin: "16px 0",
          }}
        >
          <Breadcrumb.Item style={{ color: "#3dc4e0" }}>User</Breadcrumb.Item>
          <Breadcrumb.Item>Reports</Breadcrumb.Item>
        </Breadcrumb>
        <div
          style={{
            padding: 24,
            minHeight: 360,
            background: "#ffffff",
          }}
        >
          <Row justify="space-between" align="middle">
            <Col span={8}>
              <h2>Download Expense Report</h2>
            </Col>
            <Col span={8}>
              <SearchBar expenses={expenses} />
            </Col>
          </Row>

          <Table
            columns={columns}
            dataSource={expenses}
          />
        </div>
      </div>
    </MainLayout>
  );
}

export default Report;
