import React, { useState } from "react";
import MainLayout from "../layouts/home";
import { TextField, Button, Select, MenuItem, Box, Alert } from "@mui/material";
import { Breadcrumb } from "antd";
import axios from "axios";
import { useActionData, Form } from "@remix-run/react";

export async function createExpense(
  title,
  category,
  amount,
  createdAt,
  userId
) {
  try {
    const config = { headers: { "Content-Type": "application/json" } };
    const res = await axios.post("http://localhost:4000/api/expense/create", {
      title,
      category,
      amount,
      createdAt,
      userId,
      config,
    });
    const mydata = await res?.data;
    return { mydata };
  } catch (error) {
    console.log(error.message, "error");
    return { error };
  }
}

export async function action({ request }) {
  const form = await request.formData();
  const title = form.get("title");
  const category = form.get("category");
  const amount = form.get("amount");
  const date = form.get("date");
  switch (request.method) {
    case "POST": {
      // if (typeof window !== "undefined") {
      //   const userId = localStorage.getItem("id");
      //   console.log("sfsfs", userId);
      // }
      const expenseData = await createExpense({
        title,
        category,
        amount,
        date,
        // userId: localStorage.getItem("id") || null,
        userId: "639c1738bebc82f02476e66a",
      });
      return expenseData;
    }
    default:
      return "invalid form data";
  }
}

