import React, { useState } from "react";
import {
  UserOutlined,
  DesktopOutlined,
  FileOutlined,
  HomeOutlined,
  TransactionOutlined,
  SettingOutlined,
} from "@ant-design/icons";
import {
  Layout,
  Menu,
  Avatar,
  Divider,
  Button,
  Popover,
} from "antd";
const { Header, Content, Footer, Sider } = Layout;
import { useDispatch } from 'react-redux';
import { useNavigate } from "@remix-run/react";

const navItems = [
  {
    label: "Dashboard",
    key: "/user/dashboard",
    icon: <HomeOutlined />,
  },
  {
    label: "Category",
    key: "/user/categories",
    icon: <FileOutlined />,
  },
  {
    label: "Transaction",
    key: "/user/transaction",
    icon: <TransactionOutlined />,
  },
  {
    label: "Reports",
    key: "/user/reports",
    icon: <DesktopOutlined />,
  },
  {
    label: "Settings",
    key: "/user/setting",
    icon: <SettingOutlined />,
  },
];
const MainLayout = ({ children }) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [collapsed, setCollapsed] = useState(false);

  const Logout = () => {
    localStorage.clear();
    dispatch({ type: "logout" });
    navigate("/login");
  };

  const handleMenuItem = (key) => { 
    navigate(key);
  };
  const content = (
    <>
      <Button block type="text">
        My Profile
      </Button>
      <Divider style={{ margin: "4px 0" }} />
      <Button block type="text" onClick={Logout}>
        Log out
      </Button>
    </>
  );
  return (
    <Layout
      style={{
        minHeight: "100vh",
      }}
    >
      <Sider
        collapsible
        collapsed={collapsed}
        onCollapse={(value) => setCollapsed(value)}
      >
        <div
          style={{
            height: 32,
            margin: 16,
            background: "rgba(255, 255, 255, 0.2)",
            color: "#fff",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <h2>ΣƬ</h2>
        </div>
        <Menu
          theme="dark"
          onClick={(item) => handleMenuItem(item?.key)}
          // defaultSelectedKeys={"/user/dashboard"}
          mode="inline"
          items={navItems}
        />
      </Sider>
      <Layout className="site-layout">
        <Header
          style={{
            padding: 0,
            background: "#002140",
            boxShadow: "0px 4px 8px rgb(153 170 255 / 30%)",
            // background: '#ffffff',
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
          }}
        >
         <div style={{color:'#fff', marginLeft:'20px', fontSize:'1.5rem'}}>𝔼𝕏ℙ𝔼ℕ𝕊𝔼 𝕋ℝ𝔸ℂ𝕂</div>
          <Popover placement="bottomRight" content={content} trigger="click">
            <Avatar
              style={{ background: "#ffffff33", right: "20px" }}
              size={44}
              icon={<UserOutlined />}
            />
          </Popover>
        </Header>
        <Content
          style={{
            margin: "0 16px",
          }}
        >
        {children}
        </Content>
        <Footer
          style={{
            textAlign: "center",
          }}
        >
          Expense Tracker ©2022 Created by AS.
        </Footer>
      </Layout>
    </Layout>
  );
};
export default MainLayout;
